@import url("https://fonts.googleapis.com/css?family=Quattrocento+Sans:400,700");
@import url("https://fonts.googleapis.com/css?family=Lovers+Quarrel");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  font-family: "Quattrocento Sans", sans-serif; }

h1 {
  font-size: 16rem; }

a,
a:visited {
  color: #342610;
  text-decoration: none;
  transition: 250ms cubic-bezier(0.075, 0.82, 0.165, 1) all; }
  a:hover,
  a:visited:hover {
    color: #d8b886; }

p {
  font-size: 1.5rem;
  color: #FDFBF9; }
  p:not(:last-child) {
    margin-bottom: .8rem; }

#page {
  background-position: center;
  background-size: cover;
  background-image: url(assets/BG-Parchment.jpg);
  background-repeat: no-repeat;
  padding-bottom: 10rem; }

#content {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto; }

