.site-header {
  position: absolute;
  height: 7rem;
  width: 100%;
  background-color: rgba(48, 9, 0, 0.8);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  z-index: 10; }
  .site-header ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .site-header li {
    font-size: 3rem;
    font-weight: 400;
    list-style: none;
    margin-right: 2rem; }
  .site-header a {
    color: #FDFBF9; }

.hero-homepage {
  height: 90vh;
  display: flex;
  justify-content: center;
  background: transparent;
  z-index: 0; }
  .hero-homepage__title {
    position: absolute;
    align-self: center;
    font-family: "Lovers Quarrel", cursive;
    font-weight: unset;
    color: #471500;
    z-index: 2;
    background: transparent; }
  .hero-homepage__background {
    background: transparent; }

.homepage__section {
  padding: 2.5rem;
  background: rgba(48, 9, 0, 0.8);
  border: 0.1rem solid #91472e;
  border-radius: 1rem;
  box-shadow: .2rem .2rem .8rem; }
